import React from 'react' ; 
import Icons from './Icons';



const DisplaySeason =(props) => { 
console.log(props.la)
const la = props.la
const month = new Date().getMonth();



const getSeason =(la, month) => { 
    if ( month <3 && month < 9 ) { 
        return (la <0 ? "winter" : "summer")
    } else { 
        return ( la > 0? "summer" : "winter")
    }
        
    
}









return ( <div> 
    <i><Icons pic ={getSeason(la,month)}/> </i>

    <h1> Hello {getSeason(la,month)}</h1>

    <i><Icons pic ={getSeason(la,month)}/> </i></div>)
}










export default DisplaySeason;