import React from 'react';
import ReactDOM from 'react-dom';
import SeasonDisplay from './SeasonDisplay';




class App extends React.Component { 
state = { la:"", err: ""} ;



componentDidMount () { 
  window.navigator.geolocation.getCurrentPosition(
    (position) => { 
    this.setState ({ la:position.coords.latitude})
    
    },
    (err) => { 
      this.setState ( { err:err.message })
      
    }
  );
    }
    
    

    render() { 
      if ( !this.state.la &&  this.state.err ){ 
        return(<div> 
          {this.state.err})
          </div>)
      }
      if ( this.state.la &&  !this.state.err ){ 
      return ( <SeasonDisplay la = {this.state.la} />  )
      }
      else { 
        return (<div> Swag loading</div>)
      }

    }




}




ReactDOM.render(
  <React.StrictMode>
<App/>
  </React.StrictMode>,
  document.getElementById('root')
);

